// frontend/components/CollapsibleChartCard.tsx
"use client";
import React, { useState } from "react";
import Image from "next/image";
import { ChevronDown, ChevronUp } from "lucide-react";
import MiniBarChart from "./barplot";

type Props = {
  title: string;
  description?: string;
  labels: string[];
  values: number[];
  logoPath?: string; // e.g. "/logo.png"
  color?: string;
};

export default function CollapsibleChartCard({
  title,
  description,
  labels,
  values,
  logoPath,
  color,
}: Props) {
  const [open, setOpen] = useState(true);

  return (
    <div className="bg-white rounded-2xl shadow p-4 border border-gray-200">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          {logoPath && (
            <Image
              src={logoPath}
              alt="logo"
              width={40}
              height={40}
              className="rounded-full"
            />
          )}
          <div>
            <div className="text-lg font-semibold">{title}</div>
            {description && (
              <div className="text-sm text-gray-500">{description}</div>
            )}
          </div>
        </div>

        <button
          onClick={() => setOpen((s) => !s)}
          className="p-2 rounded hover:bg-gray-50"
        >
          {open ? <ChevronUp /> : <ChevronDown />}
        </button>
      </div>

      <div
        className={`mt-4 transition-all ${
          open
            ? "max-h-[1000px] opacity-100"
            : "max-h-0 opacity-0 overflow-hidden"
        }`}
      >
        <MiniBarChart labels={labels} values={values} color={color} />
      </div>
    </div>
  );
}
